.qa-tool-info {
    border-style: solid;
    border-width: 0 1px 0 0;
    border-color: gray;
    margin: 0 10px 5px 0;
    padding: 0 10px 0 0;
    max-width: 300px;

    @media only screen and (max-width: 1024px) {
        border-width: 0 0 1px 0;
        margin: 0 0 10px 0;
        padding: 0 0 10px 0;
        max-width: 100%;
    }
}
