.qa-tooltip {
    position: absolute;
    border-radius: 0.375rem;
    border: 1px solid var(--gray-border-color);
    padding: 10px;
    background-color: var(--green-btn-bkg-color);
    color: #fff;
    font-weight: 500;
    line-height: 1.2;
    z-index: 100;
    word-wrap: break-word;
    max-width: 300px;

    &::after {
        content: " ";
        left: 10px;
        border: solid transparent;
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-width: 10px;
        bottom: 100%;
        border-bottom-color: var(--green-btn-bkg-color);
    }
    ::before {
        content: " ";
        left: 8px;
        border: solid transparent;
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
        border-width: 12px;
        bottom: 100%;
        border-bottom-color: var(--gray-border-color);
    }
}
