.input-control {
    display: block;
    padding: 6px 12px;
    background-color: #fff;
    border: 1px solid #ced4da;
    border-radius: var(--border-radius);
    width: 100%;

    &:focus {
        border-color: #13653f;
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba(60, 153, 110, 0.5);
    }
}
