.qa-tool-confirm-alert {
    width: 500px;
    border: 1px solid var(--gray-border-color);
    border-radius: var(--border-radius);
    padding: 30px;
    background-color: var(--gray-bkg-color);

    &__header {
        text-align: center;
        @media only screen and (max-width: 500px) {
            font-size: 1rem;
        }
    }

    &__btns {
        display: flex;
        justify-content: center;
    }

    @media only screen and (max-width: 500px) {
        width: 300px;
    }
}
