.qa-item-edit-mode-base {
    &__buttons {
        display: flex;

        .loading-indicator {
            width: 24px;
            height: auto;
            margin-left: -4px;
        }
    }

    &__title {
        display: flex;
        word-break: break-word;
    }

    &__question-label {
        display: block;
        margin-bottom: 0.5rem;
    }

    &__answer-label {
        margin-bottom: 0.5rem;
    }

    &__question--invalid.input-control {
        border-color: var(--red-border-color);
        background-color: var(--red-bkg-color);
    }

    &__btns-checkbox {
        display: flex;
        flex-direction: column;
        margin-left: auto;

        .qa-tool-checkbox__label {
            font-size: small;
        }
    }
}
