.qa-manager {
    display: flex;
    flex: 1;
    flex-direction: column;
    min-height: 1px;
    padding: 0 5px 5px 0;

    &__content {
        display: flex;
        flex-direction: column;
        min-height: 1px;
    }

    &__ul-container {
        overflow: auto;
    }

    &__header.header-level-2 {
        margin-bottom: 20px;
    }

    &__buttons {
        display: flex;
        padding: 10px 0 0 0;
    }

    &__button-remove-all {
        margin-left: 5px;
        margin-right: auto;
    }

    &__create-new-container {
        padding: 10px;
        border-style: solid;
        border-width: 1px;
        border-color: var(--gray-border-color);
        border-bottom-right-radius: var(--border-radius);
        border-bottom-left-radius: var(--border-radius);
        background-color: var(--gray-bkg-color);
    }

    &__warning {
        background-color: var(--red-bkg-color);
        color: var(--red-text-color);
        border: 1px solid var(--red-border-color);
        border-top-right-radius: var(--border-radius);
        border-top-left-radius: var(--border-radius);
        padding: 10px;
    }
}

.no-touch .qa-manager {
    .qa-auto-scrolling-list__item:last-child {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    &--create-new-closed {
        .qa-auto-scrolling-list__container {
            margin-bottom: 1px;
        }

        .qa-auto-scrolling-list__item:last-child {
            border-bottom-left-radius: var(--border-radius);
            border-bottom-right-radius: var(--border-radius);
        }

        .qa-manager__warning {
            border-radius: var(--border-radius);
        }
    }
}

.touch .qa-manager {
    &__content {
        flex-direction: column-reverse;
    }

    &__warning {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border-bottom-left-radius: var(--border-radius);
        border-bottom-right-radius: var(--border-radius);
    }

    &__buttons {
        flex-direction: row-reverse;
        padding: 0 0 10px 0;
    }

    &__button-remove-all {
        margin-right: 5px;
        margin-left: auto;
    }

    &__create-new-container {
        border-radius: 0;
        border-top-right-radius: var(--border-radius);
        border-top-left-radius: var(--border-radius);
    }

    .qa-auto-scrolling-list__item:first-child {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }

    &--create-new-closed {
        .qa-auto-scrolling-list__item:first-child {
            border-top-left-radius: var(--border-radius);
            border-top-right-radius: var(--border-radius);
        }

        .qa-manager__warning {
            border-radius: var(--border-radius);
        }
    }
}
