@mixin header {
    font-weight: 500;
    line-height: 1.2;
}

.header-level-1 {
    font-size: calc(1.375rem + 1.5vw);
    margin: 0;
    @include header();
}

.header-level-2 {
    color: var(--section-title-color);
    font-size: calc(1.3rem + 0.6vw);
    margin-top: 0;
    margin-bottom: 0.5rem;
    @media only screen and (max-width: 400px) {
        font-size: 1.3rem;
    }
    @media only screen and (max-width: 370px) {
        font-size: 1.2rem;
    }
    @include header();
}

.header-level-3 {
    color: var(--section-title-color);
    font-size: 1.25rem;
    margin-top: 0;
    margin-bottom: 0.5rem;
    @media only screen and (max-width: 370px) {
        font-size: 1rem;
    }
    @include header();
}

.header-level-4 {
    font-size: 1.25rem;
    margin-top: 0;
    margin-bottom: 0.5rem;
    @include header();
}
