@mixin button {
    display: inline-block;
    padding: 6px 12px;
    border-radius: var(--border-radius);
    border-width: 1px;
    border-style: solid;

    &:not(:disabled) {
        cursor: pointer;
    }

    &:focus {
        outline: 0;
    }

    &:disabled {
        pointer-events: none;
        opacity: 0.65;
    }
}

@mixin solid-button(
    $color,
    $background-color,
    $border-color,
    $hover-background-color,
    $hover-border-color,
    $active-background-color,
    $active-border-color,
    $focus-box-shadow,
    $isSolid
) {
    @include button;
    @if $isSolid {
        color: $color;
        background-color: $background-color;
    } @else {
        color: $border-color;
        background-color: transparent;
    }
    border-color: $border-color;

    &:hover {
        color: $color;
        background-color: $hover-background-color;
        border-color: $hover-border-color;
    }

    &:active {
        color: $color;
        background-color: $active-background-color;
        border-color: $active-border-color;
    }

    &:focus {
        color: $color;
        background-color: $hover-background-color;
        border-color: $hover-border-color;
        box-shadow: $focus-box-shadow;
    }

    &:disabled {
        color: $color;
        background-color: $background-color;
        border-color: $border-color;
    }
}

@mixin button-gray($isSolid) {
    @include solid-button(
        #fff,
        #6c757d,
        #6c757d,
        #5c636a,
        #565e64,
        #565e64,
        #51585e,
        0 0 0 0.25rem rgba(130, 138, 145, 0.5),
        $isSolid
    );
}

@mixin button-green($isSolid) {
    @include solid-button(
        #fff,
        #198754,
        #198754,
        #157347,
        #146c43,
        #146c43,
        #13653f,
        0 0 0 0.25rem rgba(60, 153, 110, 0.5),
        $isSolid
    );
}

.button-gray-solid {
    @include button-gray(true);
}

.button-gray-outline {
    @include button-gray(false);
}

.button-green-solid {
    @include button-green(true);
}

.button-green-outline {
    @include button-green(false);
}

.small-button {
    width: 42px;
    height: 38px;
}

.spaced {
    white-space: nowrap;
    & > *:not(:first-child) {
        margin-left: 5px;
    }
}