@use "./scss/reboot.scss";
@use "./scss/commonVars.scss";
@use "./scss/buttons.scss";
@use "./scss/inputControls.scss";
@use "./scss/headers.scss";
@use "./scss/responsive.scss";

.qa-tool {
    padding: 20px 15px 15px 20px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    min-width: 250px;

    &__header {
        margin-bottom: 20px;
        text-align: center;
    }

    @media only screen and (max-width: 800px) {
        padding: 10px 5px 5px 10px;
    }
}

.no-touch {
    .qa-tool {
        max-height: 100vh;
    }
}
